.container {
  background-color: rgb(66, 66, 66);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: 30px;
  margin: 30px auto;
  border-radius: 12px;
  background-image: linear-gradient(rgb(0, 70, 66), rgb(9, 240, 190));
}
