form {
  width: auto;
  margin: auto;
}

form input,
form label,
form button {
  margin: 10px 5px;
  text-indent: 4px;
  border-radius: 12px;
  width: 100%;
  padding: 5px 10px;
}

form button {
  padding: 5px;
  background-color: rgb(1, 5, 59);
  color: white;
  border: none;
}
