.list {
  list-style: none;
  background-color: rgb(0, 36, 34);
  color: white;
  padding: 10px 40px;
  border-radius: 12px;
  box-shadow: 0px 0px 10px rgb(1, 5, 59);
  width: 100%;
}
.item {
  display: flex;
  gap: 30px;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
}
.item div {
  /* border: 1px solid red; */
  margin: 20px;
  background-color: rgb(255, 255, 255);
  color: rgba(4, 148, 117, 0.911);
  padding: 15px;
  border-radius: 6px;
  box-shadow: 0px 0px 5px #ccc;
  font-size: 1.6rem;
}
.cut {
  cursor: pointer;
}
